import {
  Box,
  HStack,
  Stack,
  useDisclosure,
  IconButton,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  DrawerBody,
  Drawer,
  VStack,
  //useMediaQuery,
} from "@chakra-ui/react";
import { HamburgerIcon } from "@chakra-ui/icons";
import React from "react";
import HomePage from "./HomePage";
import AboutMe from "./AboutMe";
import Skills from "./Skills";
import Projects from "./Projects";
import Resume from "./Resume";
import ContactMe from "./ContactMe";

export const Navbar = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();
  //const [isNotSmallerScreen] = useMediaQuery("(min-width:600px)");

  return (
    <Stack
      style={{
        position: "sticky",
        top: 0,
        backgroundColor: "rgb(255,255,255)",
        zIndex: 1,
      }}
    >
      <HStack
        style={{ border: "1px solid red", width: "100%", height: "50px" }}
      >
        <Box
          style={{
            border: "1px solid red",
            width: "55px",
            height: "26px",
            marginLeft: "60px",
          }}
        >
          Logo
        </Box>

        <Box display={{ base: "none", md: "flex" }}>
          <Box style={{ display: "flex", gap: "2px" }} ml="30rem">
            <Box
              style={{
                border: "1px solid red",
                width: "55px",
                height: "26px",
              }}
            >
              <HomePage />
            </Box>
            <Box
              style={{
                border: "1px solid red",
                width: "55px",
                height: "26px",
              }}
            ></Box>
            <Box
              style={{
                border: "1px solid red",
                width: "55px",
                height: "26px",
              }}
            ></Box>
            <Box
              style={{
                border: "1px solid red",
                width: "55px",
                height: "26px",
              }}
            ></Box>
            <Box
              style={{
                border: "1px solid red",
                width: "55px",
                height: "26px",
              }}
            ></Box>
          </Box>
        </Box>

        {/* Resume */}

        <Box>
          <Resume ml="20rem" />
        </Box>

        {/* Hamburger icon */}

        <Box display={{ base: "flex", md: "none" }}>
          {/* <SimpleGrid columns={3}> */}
          <IconButton
            aria-label="hamburger"
            icon={<HamburgerIcon />}
            ref={btnRef}
            colorScheme="teal"
            onClick={onOpen}
          />

          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
          >
            <DrawerOverlay />
            <DrawerContent
              style={{
                backgroundColor: "rgb(14,36,49)",
                marginTop: "50px",
              }}
            >
              <DrawerCloseButton />
              {/* <DrawerHeader>Create your account</DrawerHeader> */}

              <DrawerBody>
                <VStack
                  style={{
                    border: "1px solid red",
                    width: "100px",
                    height: "160px",
                    marginTop: "10px",
                    color: "white",
                  }}
                >
                  <Box>
                    <Box>
                      <HomePage />
                    </Box>
                    <Box>
                      <AboutMe />
                    </Box>
                    <Box>
                      <Projects />
                    </Box>
                    <Box>
                      <Skills />
                    </Box>
                    <Box>Resume</Box>
                    <Box>
                      <ContactMe />
                    </Box>
                  </Box>
                </VStack>
              </DrawerBody>

              {/* <DrawerFooter>
                <Button variant="outline" mr={3} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="blue">Save</Button>
              </DrawerFooter> */}
            </DrawerContent>
          </Drawer>
          {/* </SimpleGrid> */}
        </Box>
      </HStack>
    </Stack>
  );
};
